import React, { useEffect, useState } from "react";

import { IconButton, Button, Checkbox } from "@material-ui/core";
import AddIcon from "@material-ui/icons/Add";
import EditIcon from "@material-ui/icons/Edit";
import DeleteIcon from "@material-ui/icons/Delete";
import SearchIcon from "@material-ui/icons/Search";

import axios from "axios";

const TodoForm = () => {
  const [todoList, setTodoList] = useState([]);
  const [activeItem, setActiveItem] = useState({
    id: null,
    title: "",
    completed: false,
  });
  const [searchFilter, setSearchFilter] = useState("");
  const [editing, setEditing] = useState(false);
  const [completed, setCompleted] = useState(false);

  const fetchTodos = async () => {
    const todoList = await axios.get("http://127.0.0.1:8000/api/task-list");
    setTodoList(todoList.data);
  };

  const addTodo = async () => {
    let url = "http://127.0.0.1:8000/api/task-create/";
    if (editing) {
      url = `http://127.0.0.1:8000/api/task-update/${activeItem.id}/`;
    }
    await axios.post(url, activeItem);
    fetchTodos();
    setActiveItem({
      id: null,
      title: "",
      completed: false,
    });
  };

  const startEdit = (todo) => {
    setActiveItem(todo);
    setEditing(true);
    document.querySelector(".todo").focus();
  };

  const deleteTodo = async (todo) => {
    await axios.delete(`http://127.0.0.1:8000/api/task-delete/${todo.id}/`);
    fetchTodos();
  };

  const strikeUnstrike = async (todo) => {
    todo.completed = !todo.completed;
    let url = `http://127.0.0.1:8000/api/task-update/${todo.id}/`;
    await axios.post(url, todo);
    fetchTodos();
  };

  const filterSearch = async (searchTitle) => {
    const todoList = await axios.get(
      `http://127.0.0.1:8000/api/task-list/?title=${searchTitle}`
    );
    setTodoList(todoList.data);
  };

  useEffect(() => {
    fetchTodos();
  }, []);

  return (
    <div className="todoForm">
      <div className="form">
        <form className="add" onSubmit={addTodo}>
          <input
            type="text"
            onChange={(e) =>
              setActiveItem({ ...activeItem, title: e.target.value })
            }
            className="todo"
            value={activeItem.title}
            placeholder="Your ToDo here..."
          />
          <Button startIcon={<AddIcon />}>Add</Button>
        </form>
        <div className="search">
          <input
            type="text"
            onChange={(e) => {
              setSearchFilter(e.target.value);
              filterSearch(e.target.value);
            }}
            value={searchFilter}
            placeholder="Search"
          />
          <IconButton>
            <SearchIcon />
          </IconButton>
        </div>
        <form className="completed">
          <Checkbox id="completed" onChange={(e) => setCompleted(!completed)} />
          <label htmlFor="completed">Completed</label>
        </form>
      </div>
      <div className="todos">
        {todoList.length &&
          todoList.map((todo) => {
            return !todo.completed ? (
              <span key={todo.title}>
                <h5 onClick={() => strikeUnstrike(todo)}>{todo.title}</h5>
                <div className="tools">
                  <IconButton onClick={() => startEdit(todo)}>
                    <EditIcon />
                  </IconButton>
                  <IconButton onClick={() => deleteTodo(todo)}>
                    <DeleteIcon />
                  </IconButton>
                </div>
              </span>
            ) : (
              completed && (
                <strike key={todo.id}>
                  <h5 onClick={() => strikeUnstrike(todo)}>{todo.title}</h5>
                  <div className="tools">
                    <IconButton onClick={() => deleteTodo(todo)}>
                      <DeleteIcon />
                    </IconButton>
                  </div>
                </strike>
              )
            );
          })}
      </div>
    </div>
  );
};

export default TodoForm;
